.head {
  border-color: violet;
  border-width: 1px;
  background-color: white;
  height: 100px;
  padding-left: 12vh;
  padding-right: 12vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow:0px 0px 12px 0px rgba(26,38,51,0.2);

  .div-left{
    height: 51px;
  }

  .div-right {
    height: 100%;
    width: 140px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    
    .faqs-bg{
      position: absolute;
      width: 100%;
      height: 100%;
      background: linear-gradient(
      90deg,
      rgba(179, 178, 255, 1) 0%,
      rgba(179, 178, 255, 0.5) 100%
      );
      opacity: 0.5;
    }
    .faqs {
      color: #6666FF;
      text-decoration: none;
      size: 24px;
    }
  }
}
